footer {
  background-color: #e7eaee;
}

footer .icons {
  margin-right: 0.75rem;
  cursor: pointer;
  transition: all 0.1s linear;
  aspect-ratio: attr(width) / attr(height);
}

.icons.telegram:hover,
.icons.facebook:hover {
  transform: translateY(-2px);
}

footer .block {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  width: 90%;
  margin: auto;
  min-height: 10vh;
}

.block-1 {
  width: 30%;
}

.block-1 .box p {
  color: #226500;
}

.block-2 {
  width: 40%;
  display: flex;
}

.block-2 .box {
  width: 50%;
}

.block-2 .box:nth-child(2) {
  display: flex;
  justify-content: flex-end;
}

.block-3 {
  width: 30%;
  text-align: right;
  padding-right: 1rem;
}

.box ul li {
  margin: 1.2rem;
}

.box ul li a {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 18px;
  color: #226500;
  box-shadow: inset 0 0 0 0 #226500;
  padding: 0 0.4rem;
  margin: 0 -0.25rem;
  border-radius: 2px;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.box ul li .active-link {
  background-color: #226500;
  color: #fff;
  border-radius: 2px;
  padding: 0rem 0.4rem;
}

.box ul li a:hover {
  color: #fff;
  box-shadow: inset 200px 0 0 0 #226500;
}

.box p a {
  color: #226500;
  box-shadow: inset 0 0 0 0 #226500;
  padding: 0 0.4rem;
  margin: 0 -0.25rem;
  border-radius: 2px;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.block-3 .box p a:hover {
  color: #fff;
  box-shadow: inset 250px 0 0 0 #226500;
}

@media (max-width: 1024px) {
  .box p {
    font-size: 16px;
  }

  .box ul li a {
    font-size: 16px;
  }

  .block-3 .box {
    margin-top: 2rem;
  }
}

@media screen and (max-width: 767px) {
  footer .block {
    display: block;
    padding: 1px;
  }

  .block-2 {
    width: 100%;
    display: flex;
  }

  .block-2 .box:nth-child(2),
  .block-2 .box:nth-child(1) {
    display: flex;
    justify-content: center;
  }

  .block-1,
  .block-3 {
    margin: 2rem 0;
    width: 100%;
    text-align: center;
  }
}
